.i {
    display: flex;
    height: 100vh;
 
}

.i-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}


.i-right {
    flex: 1;
    position: relative;
}

.i-left-wrapper {
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-intro {
    font-size: 30px;
    font-weight: 300;
}

.i-name {
    font-size: 60px;
}

.i-title {
    height: 50px;
    overflow: hidden;
}

.i-title-wrapper {
    height: 100%;
    animation: move 8s ease-in-out infinite alternate;
}

@keyframes move {
    25%{
        transform: translateY(-50px);
    }
    50%{
        transform: translateY(-100px);

    }
    75%{
        transform: translateY(-150px);

    }
    100%{
        transform: translateY(-200px);

    }
}

.i-title-item {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: rgb(29, 166, 184);

    display: flex;
    align-items: center;
}

.i-img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.i-bg {
    clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
    background-color: rgb(29, 166, 184);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}